    transform: none;
    display: block;
    column-count: 2;
  }
}

.footer__item {
  max-width: 250px;
}
@media (max-width: 767px) {
  .footer__item {
    margin-top: 16px;
  }
  .footer__item:first-child {
    margin-top: 39px;
  }
}

.footer__link {
  color: #ffffff;
  font-size: 14px;
  line-height: 16px;
}
@media (max-width: 767px) {
  .footer__link {
    font-size: 16px;
    line-height: 19px;
  }
}
.footer__link:focus, .footer__link:hover {
  color: #b80027;
  border-bottom: 1px solid #b80027;
  opacity: 0.7;
}
.footer__link:active {
  opacity: 0.9;
}

.footer__social {
  padding: 0;
  margin: 0;
  list-style-type: none;
}

.social {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 11px;
  margin-right: 6px;
}
@media (max-width: 1199px) {
  .social {
    margin: 0;
    padding-top: 4px;
  }
}
@media (max-width: 767px) {
  .social {
    max-width: 97px;
    padding: 0;
  }
}

.social__link {
  color: #ffffff;
}
.social__link:focus, .social__link:hover {
  outline: none;
  color: #b80027;
  opacity: 0.7;
}
.social__link:active {
  opacity: 0.9;
}

